.logo {

  $block-name:                &; // #{$block-name}__element

  display: inline-block;
  white-space: nowrap;
  width: 100px;

  @include lg {
    width: 140px;
  }

  img {
    display: block;
  }

  svg {
    width: 100%;
    height: auto;
    fill: $white;
  }
}
